import React from 'react';
import { Navigate } from 'react-router-dom';

const ProtectedRoute = ({ children }) => {
    //  key  .
    const userRole = localStorage.getItem('userRole'); // localStorage   

    if (userRole !== 'ADMIN') {
        //   'ADMIN'     
        return <Navigate to="/" />;
    }

    return children; // 'ADMIN'    
};

export default ProtectedRoute;